@import url(https://fonts.googleapis.com/css?family=Open+Sans);

body{
  /* background-image: url(background.jpg); */
  background-color: rgb(156, 194, 194);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  font-family: 'Open Sans', sans-serif;
}

/*Header*/
header{
  color: beige;
  text-align: center;
}

header a{
  text-decoration: none;
}

/*Search bar*/
.search {
  width: 30%;
  margin:auto;
  margin-top: 30px;
  margin-bottom: 30px;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 1px solid rgb(255, 255, 255);
  border-right: none;
  padding: 5px;
  height: 24px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: rgb(119, 121, 120);
  font-size: 16px;
  padding-left:10px;
}

.searchTerm:focus{
  color: rgb(37, 37, 37);
}

.searchButton{
  width: 50px;
  height: 36px;
  border: 1px solid #4aa889;
  background: linear-gradient(155deg, #5bc59c, #4aa889, #318b4c);
  text-align: center;
  color:#fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 18px;
}

.searchButton:hover{
    background: linear-gradient(155deg, #318b4c,#4aa889,#5bc59c);
}

/*Card*/

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 30%;
  border-radius: 5px;
  background-color: white;
  margin:auto;
  margin-bottom: 20px;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

img {
  border-radius: 5px 5px 0 0;
  width: 100%;
}

.container {
  padding: 2px 16px 20px 16px;
}

/*Details*/

details summary::-webkit-details-marker {
  display: none
}
summary{
  cursor: pointer;
  padding-bottom: 5px;
  color: #4aa889;
}
summary:after {
  background: #4aa889; 
  border-radius: 5px; 
  content: "+"; 
  color: #fff; 
  float: left; 
  font-size: 1.3em; 
  margin: -5px 10px 0 0; 
  padding: 0; 
  text-align: center; 
  width: 28px;
}
details[open] summary:after {
  content: "-";
}
details {
    border-bottom: 1px solid #4aa889;
    margin-bottom:15px;
}

/*Responsive*/
@media only screen and (max-width: 767px){

  .search {
    width: 80%;
  }

  .card{
    width: 85%;
  }

}